@import "../theme.sass";

.Bio {
	letter-spacing: -0.38px;
	.columns {
		align-items: center; }
	.short-bio {
		font-size: 1.1rem;
		cursor: default;
		text-align: left;
		@include desktop {
			padding-right: 1rem; } }
	.portrait {
		text-align: center;
		@include mobile {
			margin-top: 1rem; }
		@include tablet {
			max-width: 20rem;
			padding-left: 3rem; }
		img {
			max-width: 20rem;
			width: 100%; }
		.caption {
			font-size: 0.875rem;
			color: $text-light;
			margin-top: 0.25rem; } }
	.is-hidden-tablet {
		.portrait img {
			height: 6rem; } } }
