@import "../theme.sass";

.Navbar {
	@include font-roboto;
	height: 9rem;
	@include tablet {
		height: 15rem; }
	.container {
		height: 100%; }
	.row {
		display: flex;
		align-items: center;
		height: 100%; }
	.item {
		a {
			font-size: 1.25rem;
			@include mobile {
				font-size: 1.15rem; }
			font-weight: bold;
			color: $text-light;
			padding-left: 1.5rem;
			padding-right: 1.5rem;
			user-select: none;
			height: 100%;
			display: flex;
			align-items: center;
			@include mobile {
				padding-left: .75rem;
				padding-right: .75rem; }
			&:hover {
				color: $text;
				border: none; }
			&.active {
				color: $text; } }
		&:first-child a {
			padding-left: 0rem; } } }
